.gk-presentation__video {
  position: relative;
  margin-bottom: 30px;

  & iframe {
    width: 100%;
    height: 100%;
  }

  & img {
    width: 100%;
    height: 100%;
  }

  & span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 50px;
  }
}

.gk-presentation__description {
  & p {
    line-height: 22px;
  }

  & p:not(:last-of-type) {
    margin-bottom: 20px;
  }
}

@media (min-width: @tablet-width) {
  .gk-presentation__wrapper p {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 75%;
  }

  .gk-presentation__video {
    height: 500px;
  }
}

@media (min-width: @desktop-width) {
  .gk-presentation__wrapper p {
    width: 55%;
  }

  .gk-presentation__video {
    height: 700px;
  }
}
