.wrapper-gk--survey {
  padding: 15px;
  border-radius: 5px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);

  @media (min-width: @desktop-width) {
    padding: 0;
    box-shadow: none;
  }
}

.survey__layout {
  display: flex;
  flex-direction: column;

  & p {
    color: #737a8e;
  }

  & p:first-of-type {
    margin-bottom: 15px;
  }

  & ul {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 20px;
  }

  & ul li {
    padding: 8px;
    background-color: #f2f5f7;
    border-radius: 5px;
  }

  & p:last-of-type {
    font-size: 14px;
  }
}

.survey__contacts {
  display: grid;
  grid-template-columns: max-content;
  grid-template-rows: 48px;
  margin-bottom: 10px;

  & .button-main {
    grid-row: 1/-1;
    grid-column: 1/-1;
    order: 1;
  }

  & a {
    grid-row: 1/1;
    grid-column: 1/-1;
    margin: auto;
    font-size: 22px;
    color: var(--basic-black);
    font-weight: 500;
  }
}

@media (min-width: @tablet-width) {
  .survey__layout {
    max-width: 700px;

    & ul {
      gap: 20px;
    }

    & p:last-of-type {
      font-size: inherit;
    }
  }
}

@media (min-width: @desktop-width) {
  .survey__layout {
    position: relative;
    max-width: 800px;
    min-width: 600px;

    &::after {
      position: absolute;
      content: "";
      top: 40%;
      transform: translateY(-50%);
      right: 50px;
      width: 110px;
      height: 110px;
      background-image: url(data:image/svg+xml;base64,);
    }

    & p:last-of-type {
      font-size: inherit;
    }
  }
}

.survey--modal {
  background-color: var(--white);

  @media (min-width: @desktop-width) {
    width: 800px;

    & .survey__layout {
      &::after {
        position: absolute;
        content: "";
        top: 50%;
        right: 25px;
      }
    }
  }
}
